/** @format */

.experience__container {
	display: grid;
	grid-template-columns: 1fr 1fr;
	gap: 2rem;
}

.experience__container > div {
	background: #2c2c6c;
	padding: 2rem 4rem;
	border-radius: 2rem;
	border: 1px solid transparent;
	transition: all 450ms ease;
}

.experience__container > div:hover {
	background: transparent;
	border-color: #2c2c6c;
	cursor: default;
}

.experience__container > div h3 {
	text-align: center;
	margin: bottom 2rem;
	color: #4db5ff;
}

.experience__content {
	display: grid;
	grid-template-columns: 1fr 1fr;
	gap: 2rem;
}

.experience__details-icon {
	margin-top: 6px;
	color: #4db5ff;
}

.text-light {
	font-weight: lighter;
	color: #9e9ec2;
}

@media screen and (max-width: 1024px) {
	.experience__container {
		grid-template-columns: 1fr;
	}
	.experience__container > div {
		width: 80%;
		padding: 2rem;
		margin: 0 auto;
	}
	.experience__content {
		padding: 1rem;
	}
}

/* medium devices */
@media screen and (max-width: 600px) {
	.experience__container {
		gap: 1rem;
	}
	.experience__container > div {
		width: 100%;
		padding: 2rem 1rem;
	}
}
