/** @format */

.about__container {
	display: grid;
	grid-template-columns: 35% 50%;
	gap: 15%;
}

.about__me {
	width: 100%;
	aspect-ratio: 1/1;
	border-radius: 2rem;
	/* background: linear-gradient(45deg, transparent, #4db5ff); */
	display: grid;
	place-items: center;
}

.about__me-image {
	transform: rotate(0);
	border-radius: 2rem;
}
.aboutMeImage {
	border-radius: 1rem 5rem 1rem 5rem;
}

.about__me-image:hover {
	border-radius: 2rem;
	animation: shake 0.9s;
	animation-iteration-count: infinite;
	transition: all 4000ms ease;
}

.about__cards {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	gap: 1.5rem;
}

.about__card {
	background: #2c2c6c;
	border: 1px solid transparent;
	border-radius: 1rem;
	padding: 2rem;
	text-align: center;
}

.about__card:hover {
	background: transparent;
	border-color: rgba(77, 181, 255, 0.4);
	transition: all 400ms ease;
	cursor: default;
}

.about__icon {
	color: #4db5ff;
	font-size: 1.4rem;
	margin-bottom: 0.5rem;
}

.about__card h5 {
	font-size: 0.95rem;
}

.about__card small {
	font-size: 0.7rem;
	color: rbga(255, 255, 255, 0.6);
}

.about__content p {
	margin: 2rem 0 2.6rem;
	color: rbga(255, 255, 255, 0.6);
}

@media screen and (max-width: 1024px) {
	.about__container {
		grid-template-columns: 1fr;
		gap: 0;
	}
	.about__me {
		width: 50%;
		margin: 2rem auto 4rem;
	}
	.about__content p {
		margin: 1rem 0 1.5rem;
	}
}

/* medium devices */
@media screen and (max-width: 600px) {
	.about__me {
		width: 65%;
		margin: 0 auto 3rem;
	}
	.about__cards {
		grid-template-columns: 1fr 1fr;
		gap: 1rem;
	}
	.about__content {
		text-align: center;
	}
	.about__content p {
		margin: 1.5rem 0;
	}
}
